// API
const TRANSLATION_APIS = {
  mymemory: {
    name: 'MyMemory',
    description: 'API1000',
    baseUrl: 'https://api.mymemory.translated.net',
    status: 'free',
    rateLimit: '1000/day'
  },
  
  libre: {
    name: 'LibreTranslate',
    description: '17',
    baseUrl: 'https://libretranslate.de/translate',
    status: 'free',
    rateLimit: '20/min'
  },
  
  google_web: {
    name: 'Google Web',
    description: 'GoogleAPI',
    baseUrl: 'https://translate.googleapis.com/translate_a/single',
    status: 'unofficial',
    rateLimit: 'unlimited'
  }
};

/**
 * MyMemoryAPI
 */
async function translateWithMyMemory(text, targetLang = 'zh', sourceLang = 'auto') {
  try {
    const langPair = sourceLang === 'auto' ? `autodetect|${targetLang}` : `${sourceLang}|${targetLang}`;
    const url = `${TRANSLATION_APIS.mymemory.baseUrl}/get?q=${encodeURIComponent(text)}&langpair=${langPair}`;
    
    const response = await fetch(url);
    const data = await response.json();
    
    if (data.responseStatus === 200 && data.responseData) {
      return data.responseData.translatedText;
    }
    
    throw new Error(data.responseDetails || '');
  } catch (error) {
    console.error('MyMemory:', error);
    throw error;
  }
}

/**
 * LibreTranslateAPI
 */
async function translateWithLibre(text, targetLang = 'zh', sourceLang = 'auto') {
  try {
    // 
    const langMap = {
      'zh': 'zh',
      'en': 'en',
      'ja': 'ja',
      'ko': 'ko',
      'fr': 'fr',
      'de': 'de',
      'es': 'es',
      'ru': 'ru'
    };
    
    const target = langMap[targetLang] || 'zh';
    const source = sourceLang === 'auto' ? 'auto' : (langMap[sourceLang] || 'en');
    
    const response = await fetch(TRANSLATION_APIS.libre.baseUrl, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        q: text,
        source: source,
        target: target,
        format: 'text'
      })
    });
    
    const data = await response.json();
    
    if (data.translatedText) {
      return data.translatedText;
    }
    
    throw new Error(data.error || '');
  } catch (error) {
    console.error('LibreTranslate:', error);
    throw error;
  }
}

/**
 * Google WebAPI ()
 */
async function translateWithGoogleWeb(text, targetLang = 'zh', sourceLang = 'auto') {
  try {
    const url = `${TRANSLATION_APIS.google_web.baseUrl}?client=gtx&sl=${sourceLang}&tl=${targetLang}&dt=t&q=${encodeURIComponent(text)}`;
    
    const response = await fetch(url);
    const data = await response.json();
    
    if (data && data[0] && data[0][0] && data[0][0][0]) {
      return data[0][0][0];
    }
    
    throw new Error('Google');
  } catch (error) {
    console.error('Google Web:', error);
    throw error;
  }
}

