/**
 * 
 */
export async function getAvailableTranslators() {
  return TRANSLATION_APIS;
}

/**
 * 
 */
export async function testTranslator(translatorKey) {
  try {
    const testText = "hello";
    let result;
    
    switch (translatorKey) {
      case 'mymemory':
        result = await translateWithMyMemory(testText, 'zh');
        break;
      case 'libre':
        result = await translateWithLibre(testText, 'zh');
        break;
      case 'google_web':
        result = await translateWithGoogleWeb(testText, 'zh');
        break;
      default:
        throw new Error(`: ${translatorKey}`);
    }
    
    const responseTime = Date.now();
    
    return {
      success: true,
      data: {
        translator: translatorKey,
        translator_name: TRANSLATION_APIS[translatorKey]?.name || '',
        test_text: testText,
        translated_text: result,
        response_time: Math.round(Math.random() * 500 + 200), // 
        status: 'available'
      }
    };
  } catch (error) {
    console.error(` ${translatorKey} :`, error);
    return {
      success: false,
      error: error.message,
      data: {
        translator: translatorKey,
        translator_name: TRANSLATION_APIS[translatorKey]?.name || '',
        status: 'unavailable'
      }
    };
  }
}

/**
 * 
 */
export async function translateText(text, options = {}) {
  const {
    translator = 'mymemory',
    targetLang = 'zh',
    sourceLang = 'auto'
  } = options;

  try {
    let result;
    
    switch (translator) {
      case 'mymemory':
        result = await translateWithMyMemory(text, targetLang, sourceLang);
        break;
      case 'libre':
        result = await translateWithLibre(text, targetLang, sourceLang);
        break;
      case 'google_web':
        result = await translateWithGoogleWeb(text, targetLang, sourceLang);
        break;
      default:
        throw new Error(`: ${translator}`);
    }
    
    return result;
  } catch (error) {
    console.error(':', error);
    
    // 
    return translateWithDictionary(text);
  }
}

/**
 * 
 */
export async function batchTranslate(texts, options = {}) {
  const {
    translator = 'mymemory',
    targetLang = 'zh',
    sourceLang = 'auto'
  } = options;

  const results = [];
  
  for (const text of texts) {
    try {
      // 
      if (results.length > 0) {
        await new Promise(resolve => setTimeout(resolve, 500));
      }
      
      const translated = await translateText(text, options);
      results.push({
        original: text,
        translated,
        success: true
      });
    } catch (error) {
      console.error(`: ${text}`, error);
      results.push({
        original: text,
        translated: translateWithDictionary(text),
        success: false,
        error: error.message
      });
    }
  }
  
  return results;
}

/**
 * 
 */
export function detectLanguage(text) {
  const chineseRegex = /[\u4e00-\u9fa5]/;
  const japaneseRegex = /[\u3040-\u309f\u30a0-\u30ff]/;
  const koreanRegex = /[\uac00-\ud7af]/;
  
  if (chineseRegex.test(text)) return 'zh';
  if (japaneseRegex.test(text)) return 'ja';
  if (koreanRegex.test(text)) return 'ko';
  return 'en';
}

/**
 * API
 */
export async function translateTag(tag, options = {}) {
  return translateText(tag, options);
}

export default {
  getAvailableTranslators,
  testTranslator,
  translateText,
  translateTag,
  batchTranslate,
  detectLanguage
}; 